// Централизованное хранилище Vuex
import Vue from 'vue';
import Vuex from 'vuex';
// import products from '@/data/products';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [
      // { productId: 3007, amount: 5 },
    ],
    userAccessKey: null,
    cartProductsData: [],

    cartProductsLoading: false,

    orderInfo: null,
  },
  mutations: {
    /* // addProductToCart(state, payload) {
    addProductToCart(state, { productId, amount }) {
      // eslint-disable-next-line no-shadow
      const item = state.cartProducts.find((item) => item.productId === productId);

      if (item) {
        item.amount += amount;
      } else {
        state.cartProducts.push({
          // productId: payload.productId,
          productId, // productId = productId
          // amount: payload.amount,
          amount, // amount = amount
        });
      }
    }, */
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((item) => item.productId === productId);

      if (item) {
        item.amount = amount;
      }
    },
    // deleteCartProduct(state, productId) {
    //   state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    // },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      // eslint-disable-next-line arrow-body-style
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        };
      });
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        // eslint-disable-next-line prefer-destructuring
        const product = state.cartProductsData.find((p) => p.product.id === item.productId).product;
        return {
          ...item,
          // product: products.find((p) => p.id === item.productId),
          // product,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
  },

  actions: {
    /*
    loadCart(context) {
      this.cartProductsLoading = true;
      this.cartProductsLoadingFailed = false;
      // eslint-disable-next-line prefer-template
      return axios
        // eslint-disable-next-line prefer-template
        .get(API_BASE_URL + '/api/baskets', {
        // userAccessKey: context.state.userAccessKey,
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
      // eslint-disable-next-line arrow-parens
      .then(response => {
        if (!context.state.userAccessKey) {
          localStorage.setItem('userAccessKey', response.data.user.accessKey);
          context.commit('updateUserAccessKey', response.data.user.accessKey);
        }
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts', response.data.items);
      })
      // eslint-disable-next-line no-return-assign
      .catch(() => this.cartProductsLoadingFailed = true)
      // eslint-disable-next-line no-return-assign
      .then(() => this.cartProductsLoading = false);
    },
    */

    loadCart(context) {
      // eslint-disable-next-line no-param-reassign
      // context.state.cartProductsLoading = true;
      this.state.cartProductsLoading = true;
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        // eslint-disable-next-line no-param-reassign
        // .then(() => context.state.cartProductsLoading = true)
        .then(() => {
          return axios
            .get(API_BASE_URL + '/api/baskets', {
              params: {
                userAccessKey: context.state.userAccessKey,
              },
            })
            .then((response) => {
              if (!context.state.userAccessKey) {
                localStorage.setItem('userAccessKey', response.data.user.accessKey);
                context.commit('updateUserAccessKey', response.data.user.accessKey);
              }
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts', response.data.items);
            })
        })
        // eslint-disable-next-line no-param-reassign
        // .then(() => context.state.cartProductsLoading = false)
        .then(() => this.state.cartProductsLoading = false);
    },
    /*
    addProductToCart(context, { productId, amount }) {
      // eslint-disable-next-line prefer-template
      return axios
        // eslint-disable-next-line prefer-template
        .post(API_BASE_URL + '/api/baskets/products', {
        // eslint-disable-next-line object-shorthand
        productId: productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
      .then((response) => {
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      });
    },
    */

    addProductToCart(context, { productId, amount }) {
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        .then(() => {
          return axios
            .post(API_BASE_URL + '/api/baskets/products', {
              productId: productId,
              quantity: amount,
          }, {
              params: {
                userAccessKey: context.state.userAccessKey,
              },
           })
          .then((response) => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          })
        });
    },

    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });
      if (amount < 1) {
        return;
      }
      // eslint-disable-next-line consistent-return
      return axios
        // eslint-disable-next-line prefer-template
        .put(API_BASE_URL + '/api/baskets/products', {
        // eslint-disable-next-line object-shorthand
        productId: productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
      .then((response) => {
        context.commit('updateCartProductsData', response.data.items);
      })
      .catch(() => {
        context.commit('syncCartProducts');
      });
    },

    deleteCartProduct(context, { productId }) {
      return axios
      .delete(API_BASE_URL + '/api/baskets/products', {
        data: {
          productId,
        },
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
      .then((response) => {
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      });
    },
    loadOrderInfo(context, orderId) {
      return axios
        .get(API_BASE_URL + '/api/orders/' + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
  },
});
