<!-- eslint-disable max-len -->
<template>
  <ul class="catalog__pagination pagination">
    <!-- Стрелка влево, назад -->
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow"
       :class="{'pagination__link--disabled': page === 1}"
       href="#" aria-label="Предыдущая страница" @click.prevent="paginationBack(page)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>

    <!-- Ссылка на первую страницу -->
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link"
       :class="{'pagination__link--current': pageNumber === page}"
       href="#" @click.prevent="pagination(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>

    <!-- Стрелка вправо, вперёд -->
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow"
       :class="{'pagination__link--disabled': page === pages}"
       href="#" aria-label="Следующая страница" @click.prevent="paginationNext(page)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  // page - Значение текущей страницы
  // count - кол-во товаров общее
  // perPage - кол-во товаров на одной странице
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      // Округляем в большую сторону
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    pagination(page) {
      this.$emit('paginate', page);
    },
    paginationNext(page) {
      if (page < this.pages) {
        this.$emit('paginate', page + 1);
      }
    },
    paginationBack(page) {
      if (page > 1) {
        this.$emit('paginate', page - 1);
      }
    },
  },
  model: {
    // Свойство, которое передаётся
    prop: 'page',
    // Событие, которое передаем будет называться paginate
    event: 'paginate',
  },
};
</script>
