<template>
  <li class="catalog__item">
    <!-- <a class="catalog__pic" href="#"
     @click.prevent="goToPage('product', {id: product.id})">
      <img v-bind:src="product.image" v-bind:alt="product.title">
    </a> -->
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img v-bind:src="product.image" v-bind:alt="product.title">
    </router-link>
    <h3 class="catalog__title">
      {{ product.title }}
    </h3>
    <span class="catalog__price">
        {{ product.price | numberFormat }} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="element in productColors" :key="element.id">
        <label class="colors__label">
          <!-- <input class="colors__radio sr-only" type="radio" value="#73B6EA" v-model="color"> -->
          <input class="colors__radio sr-only" type="radio" value="#73B6EA" v-model="color">
          <!-- <span class="colors__value" style="background-color: #73B6EA;"> -->
          <!-- eslint-disable-next-line vue/no-parsing-error -->
          <!-- <span class="colors__value" :style="`background-color: ${productColors[1].code};`"> -->
          <span class="colors__value"
           :style="`background-color: ${element.code};`">
          </span>
        </label>
      </li>

      <!-- <li class="colors__item">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" value="#8BE000" v-model="color">
          <span class="colors__value" style="background-color: #8BE000;">
          </span>
        </label>
      </li> -->

      <!-- <li class="colors__item">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" value="#222"
            v-model="color">
          <span class="colors__value" style="background-color: #222;"></span>
        </label>
      </li> -->
    </ul>
  </li>
</template>

<script>
// import eventBus from '@/eventBus';
import goToPage from '@/helpers/goToPage';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      color: '#222',
      // Список цветов у товара
      productColors: [],
    };
  },

  props: ['product'],
  filters: {
    numberFormat,
  },

  // Методы (функции)
  methods: {
    // goToPage(pageName, pageParams) {
    //   eventBus.$emit('goToPage', pageName, pageParams);
    // },
    goToPage,
    loadProductColors() {
      axios
        // eslint-disable-next-line prefer-template
        .get(API_BASE_URL + '/api/products/' + this.product.id)
        // eslint-disable-next-line arrow-parens, no-return-assign
        .then(response => this.productColors = response.data.colors);
    },
  },

  // Hooks
  created() {
    this.loadProductColors();
  },
};
</script>
