<!-- eslint-disable max-len -->
<template>
  <!-- <li class="cart__item product"
   v-for="item in $store.getters.cartDetailProducts" :key="item.productId"> -->
  <!-- <li class="cart__item product" v-for="item in products" :key="item.productId"> -->
  <li class="cart__item product">
    <div class="product__pic">
      <!-- <img src="img/phone-square-3.jpg" width="120" height="120" srcset="img/phone-square-3@2x.jpg 2x" alt="Название товара"> -->
      <img :src="item.product.image" width="120" height="120"
        :alt="item.product.title">
    </div>
    <h3 class="product__title">
      <!-- Смартфон Xiaomi Redmi Note 7 Pro 6/128GB -->
      {{ item.product.title }}
    </h3>
    <!-- <p class="product__info">
      Объем: <span>128GB</span>
    </p> -->
    <span class="product__code">
      <!-- Артикул: 1501230 -->
      Артикул: {{ item.product.id }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="deleteOneProduct">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <!-- <input type="text" value="1" name="count"> -->
      <!-- <input type="text" :value="item.amount" name="count"> -->
      <input type="text" v-model.number="amount" name="count">

      <button type="button" aria-label="Добавить один товар" @click.prevent="addOneProduct">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      <!-- 18 990 ₽ -->
      {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>

    <!-- <button class="product__del button-del" @click.prevent="deleteProduct(item.productId)" -->
    <button class="product__del button-del" @click.prevent="deleteProduct"
     type="button" aria-label="Удалить товар из корзины">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
// import { mapMutations, mapActions } from 'vuex';
// import { mapMutations } from 'vuex';
import { mapActions } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        // eslint-disable-next-line max-len
        // this.$store.commit('updateCartProductAmount', { productId: this.item.productId, amount: value });
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    // ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
    // ...mapMutations(['syncCartProducts']),
    ...mapActions(['deleteCartProduct']),
    // deleteProduct(productId) {
    //   this.$store.commit('deleteCartProduct', productId);
    // },
    deleteProduct() {
      // this.deleteCartProduct({ productId: this.product.id });
      this.deleteCartProduct({ productId: this.item.productId });
    },

    addOneProduct() {
      this.amount += 1;
    },
    deleteOneProduct() {
      if (this.amount >= 1) {
        this.amount -= 1;
      }
    },
  },
  // watch: {
  //   syncCartProducts: {
  //     handler() {
  //       this.deleteProduct();
  //     },
  //   },
  //   handler() {
  //     this.deleteProduct();
  //   },
  // },
};
</script>
