<!-- eslint-disable import/no-unresolved -->
<!-- eslint-disable import/extensions -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
       :price-from.sync="filterPriceFrom"
       :price-to.sync="filterPriceTo"
       :category-id.sync="filterCategoryId"
       :color-id.sync="filterColorId">
      </ProductFilter>

      <section class="catalog">

        <div v-if="productsLoading">
          Загрузка товаров...
        </div>
        <div v-if="productsLoadingFailed">
          Произошла ошибка при загрузке товаров :(
          <button @click="loadProducts">
            Попробуйте ещё раз
          </button>
        </div>

        <ProductList v-bind:products="products">
          <ProductItem></ProductItem>
        </ProductList>

        <BasePagination v-model="page"
         :count="countProducts"
         :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import ProductItem from '@/components/ProductItem.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  components: {
    ProductList,
    ProductItem,
    BasePagination,
    ProductFilter,
  },

  data() {
    return {
      page: 1, // текущая страница
      productsPerPage: 3, // кол-во товаров на странице
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,

      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },

  computed: {
    products() {
      // const offset = (this.page - 1) * this.productsPerPage;
      // return this.filterProducts.slice(offset, offset + this.productsPerPage);
      // return this.productsData.items;
      // return this.productsData ? this.productsData.items : [];
      return this.productsData
      ? this.productsData.items.map(product => {
        return {
          ...product,
          image: product.image.file.url
        }})
      : [];
    },
    countProducts() {
      // return this.filterProducts.length;
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
        .get(`${API_BASE_URL}/api/products/`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColorId,
          },
        })
        .then((response) => this.productsData = response.data)
        .catch(() => this.productsLoadingFailed = true)
        .then(() => this.productsLoading = false)
      }, 0);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
